.container.testimonials__container {
    width: 40%;
    padding: 64px;
}

.client__avatar {
    width: 64px;
    aspect-ratio: 1/1;
    overflow: hidden;
    border-radius: 50%;
    margin: 0 auto 16px;
    border: 6 solid var(--color-primary-variant);
}

.testimonial {
    background: var(--color-bg-variant);
    text-align: center;
    padding: 32px;
    border-radius: 32px;
    user-select: none;
}

.client__review {
    color: var(--color-light);
    font-weight: 300;
    display: block;
    width: 80%;
    margin: 13px auto 0;
}

